.animation{zoom:120%;position: absolute;transform:translateY(-10em);margin-left: 37em;}
.media_animation{position: absolute;transform:translateY(-9.5em);zoom:120%;right:-6em}
.animation_2{position: absolute;transform:translateY(4.5em);zoom:120%;right:40em}
.animation_3{position: absolute;bottom:6em;zoom:120%;right:4em;transform: rotate(10deg);}
