      .timeline{
        width:100%;
        padding: auto;
        
      }
      .timeline:before{
        left:0px;
        padding:auto;
      }
      .timeline ul li:nth-child(odd),
      .timeline ul li:nth-child(even){
        width:100%;
        text-align:left;
       padding:100px; 
      }
      
      .timeline ul li:nth-child(odd):before
        {
        top:-18px;
        left:16px;
        
      
      }
      .timeline ul li:nth-child(even):before
      { 
        top:-18px;
        left:16px;
       
      }
      
        .timeline ul li:nth-child(odd) .left_content,
      .timeline ul li:nth-child(even) .left_content
      {
        top:-30px;
        left:50px;
        
      }
      .right_content{
        padding:1px;
        width: 100%;
        
        font-size: 1.5em;
        display: inline;
      
        
      }
      .right_content h2{
        padding:1px;
        width: 10em;
        font-size: 1em;
        transform:translateX(-60px);
      display: inline-block;}
        .right_content p{
          padding:1px;
          width: 10em;
          font-size: 1em;
        padding:10px;
      display:inline-block}
        
      
      .left_content{padding:1px;
        width: 18em;
      }
      .left_content h3{font-size: 2em;}
      .right_content p{font-size: 0.7em;width: 200%;padding:0em 2em;display: inline-block;transform:translateX(-90px)}

      
      
      .timeline:before {
        content: '';
        position:absolute;
        height:90%;
